import React, { useState, useEffect } from 'react';
import { sampleData } from '../../sampleData/sampleEvents';
import { mediaData } from '../../sampleData/sampleMedia';
import logo from '../../assets/img/sample/sampleEvent.png';
import calendar from '../../assets/icon/ico_calendar.png';
import location from '../../assets/icon/ico_location.png';
import ticket from '../../assets/icon/ico_tickets.png';
import rticket from '../../assets/icon/ico-ticket-white.png';
import media from '../..//assets/img/sample/sampleMedia.png'
import './style.scss';
import { IoArrowBackCircleOutline } from "react-icons/io5";
import { IoMdShareAlt } from "react-icons/io";
import { BsPlayCircle } from "react-icons/bs"
import { Link } from 'react-router-dom';
import { IconContext } from "react-icons";
import moment from 'moment';
import { Drawer } from '@mui/material';
import backg from '../../assets/img/bg/success-bg.png';
import PhoneInput from 'react-phone-number-input'
import 'react-phone-number-input/style.css'
import { QRCodeSVG } from 'qrcode.react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { sampleABOs } from '../../sampleData/sampleABOs';

