const EventDetails = (props) => {
    console.log(props)
    const [displayBio, setdisplayBio] = useState(false);
    //displayBio is value, setdisplayBio is function, false is default value
    const [event, setEvent] = useState(null);
    //const [videoData, setMediaData] = useState(null);
    const [confirm, setConfirm] = useState(true);
    const [button, setButton] = useState(false);
    const [change, setChange] = useState(false);
    const [add, setAdd] = useState(false);
    const [value, setValue] = useState();
    const [cancel, setCancel] = useState(false);

    const showAlert = () => {
        alert("This is a share button");
        return;
    }

    const registrationNumber = Math.round(Math.random() * 900000)
    const registrationDate = moment().format('MMMM DD YYYY HH:mm');



    sampleData.map(event => {
        event.start = new Date(event.startTime);
        event.end = new Date(event.endTime);
        event.startDate = moment(event.start).format("dddd, MMMM Do YYYY");
        event.regisDate = moment(event.start).format("MMMM Do YYYY");
        event.eventStart = moment(event.start).format("HH:mm");
        event.eventEnd = moment(event.end).format("HH:mm");
        event.eventRange = `${event.eventStart} - ${event.eventEnd}`
    })


    useEffect(() => {
        const id = props.match.params.id
        console.log(id, parseInt(id)); //parseInt is convert id to integer
        let thisEvent = sampleData.find(e => e.id === parseInt(id)) //e.id === id is different data type, so need parseInt
        setEvent(thisEvent)
        //setMediaData(mediaData);
        console.log(thisEvent);
        console.log(mediaData);
        console.log(window.location.pathname);

        var classname = document.getElementsByClassName("amway-button");
        console.log(classname)
        if (window.location.pathname === `/module/${id}/registered`) {
            setConfirm(true)
            setChange(true)
            return;
        } else {
            setConfirm(false)
            return;
        }
    }, []); //first action after return 


    return (
        <>
            <div className='mobile-header'>
                <Link to="/">
                    <IconContext.Provider value={{ size: "2em" }}>
                        <div className='arrow-back'><IoArrowBackCircleOutline /></div>
                    </IconContext.Provider>
                </Link>
            </div>
            {event && <div className={`event-details ${change ? "registered" : ""}`}>
                <div className='event-details-thumbnail'><img src={logo} /></div>
                <div className={`event-details-wrapper ${change ? "registered" : ""}`}>
                    <div className='event-details-title'>
                        <div className={change ? 'event-details-registered' : ''}>
                            <div className={change ? 'registeredTag' : 'registeredTag-n'}><img src={rticket} /> Registered
                            </div>
                        </div>{event.title}
                    </div>
                    {displayBio ? (<div className='event-details-desc'>{event.desc}
                        <br /><div className='event-details-link' onClick={() => setdisplayBio(false)}> View Less </div></div>
                    ) : (
                        <div className='event-details-link' onClick={() => setdisplayBio(true)}> Read More </div>)}
                    <IconContext.Provider value={{ size: "2em" }}>
                        <div className='event-details-button'>
                            <IoMdShareAlt onClick={showAlert} />
                            {change ? (
                                <div className='event-register-button'>
                                    <div className='amway-button' onClick={() => setButton(true)}>View Ticket</div>
                                </div>) : (<div className='event-register-button'>
                                    <Link to={`/register/${event.id}`}>
                                        <div className='amway-button'>Register</div>
                                    </Link>
                                </div>)}
                        </div>
                    </IconContext.Provider>
                </div>
                <div className='event-details-cards'>
                    <div className='title'>Event Details</div>
                    <div className='item'>
                        <div className='icon'><img src={calendar} /></div>
                        <div className='info'>{event.startDate}<br />{event.eventRange}</div>
                    </div>
                    <div className='item'>
                        <div className='icon'><img src={location} /></div>
                        <div className='info'>Hosted virtually via Zoom</div>
                    </div>
                    <div className='item'>
                        <div className='icon'><img src={ticket} /></div>
                        <div className='warning'>Only {event.ticketLeft} tickets left</div>
                    </div>
                </div>
                <div className='event-media-list'>
                    <div className='title'>Media</div>
                    <div className='media-list'>
                        {mediaData.map((video, i) => {
                            return (
                                <div key={i} className='media-list-item'>
                                    <div className='media-list-thumb'>
                                        <img src={media} />
                                        <IconContext.Provider value={{ size: "1em" }}>
                                            <div className='play-icon'>
                                                <BsPlayCircle />
                                            </div>
                                        </IconContext.Provider>
                                    </div>
                                    <div className='media-list-item-title'>
                                        {video.title}
                                    </div>
                                </div>
                            )
                        })
                        }

                    </div>
                </div>
                {change ? (
                    <div className='event-register'>
                        <div className='amway-button' onClick={() => setButton(true)}>View Ticket</div>
                    </div>) : (<div className='event-register'>
                        <Link to={`/register/${event.id}`}>
                            <div className='amway-button'>Register</div>
                        </Link>
                    </div>)}


                <Drawer open={confirm} className="event-registration">
                    <div className='success-wrapper'>
                        <div className='successInfo-box'>
                            <img className='successInfo-box-bg' src={backg} />
                            <div className='successInfo-box-title'>
                                Registration Saved
                            </div>
                            <div className='successInfo-box-info'>
                                You&apos;re all set!
                            </div>
                            <div className='successInfo-box-desc'>
                                You reserved [2] tickets to {event.title}. You and your guest will receive a confirmation email within 24 hours.
                            </div>
                        </div>
                        <div className='successInfo-details'>
                            <div className='successInfo-details-title'>
                                Registration Details
                            </div>
                            <div className='successInfo-details-field'>
                                <div className='label'>Registration number</div>
                                <div className='value'>{registrationNumber}</div>
                            </div>
                            <div className='successInfo-details-field'>
                                <div className='label'>Registration date</div>
                                <div className='value'>{registrationDate}</div>
                            </div>
                            <div className='successInfo-details-field'>
                                <div className='label'>Order total</div>
                                <div className='value'>FREE</div>
                            </div>
                            <div className='successInfo-details-field'>
                                <div className='label'>Attendees</div>
                                <div className='value'>3</div>
                            </div>
                            <div className='successInfo-details-buttons'>
                                <div className='calendar'> Add to calendar</div>
                                <div className='close' onClick={() => setConfirm(false)}>Close</div>
                            </div>
                        </div>
                    </div>
                </Drawer>

                <Drawer open={button} onClose={!button} className='confirm-screen' anchor="bottom">
                    <div className='confirm_container popEvent'>
                        <div className='body_container'>
                            <div className='close_bar'>
                                <button type='button' className='close_button'>
                                </button>
                                <button className='close_notch' onClick={() => setButton(false)}></button>
                            </div>
                            <div className='pop-wrapper'>
                                <div className='content'>
                                    <div className='event-tickets'>
                                        <div className='successInfo-details'>
                                            <div className='successInfo-details-title'>Registration Details</div>
                                            <div className='successInfo-details-field'>
                                                <div className='label'>Registration number</div>
                                                <div className='value'>{registrationNumber}</div>
                                            </div>
                                            <div className='successInfo-details-field'>
                                                <div className='label'>Registration date</div>
                                                <div className='value'>{registrationDate}</div>
                                            </div>
                                            <div className='successInfo-details-field'>
                                                <div className='label'>Order total</div>
                                                <div className='value'>FREE</div>
                                            </div>
                                            <div className='successInfo-details-field'>
                                                <div className='label'>Attendees</div>
                                                <div className='value'>3</div>
                                            </div>
                                            <div className='successInfo-details-buttons'>
                                                <div className='amway-button'>Contact Organizer</div>
                                                <div className='amway-button' onClick={() => setCancel(true)}>Cancel Registration</div>
                                            </div>
                                        </div>
                                        <div className='etickets'>
                                            <div className='etickets-header'>
                                                <div className='etickets-header-title'>Attendee Tickets (3)</div>
                                                <div className='etickets-header-add' onClick={() => setAdd(true)}>Add Attendee</div>
                                            </div>
                                            <div className='eticket-slider'>
                                                <Carousel swipeable={true} showStatus={false} emulateTouch={true}>
                                                {sampleABOs.map((info, k) => {
                                                return(
                                                    <div className='eticket-card' key={k}>
                                                        <div className='eticket-card-wrapper'>
                                                            <div className='eticket-card-partyName'>{info.partyName}</div>
                                                            <div className='eticket-card-fullName'>{info.fullName}</div>
                                                            <QRCodeSVG className='eticket-card-ticketQR' value="https://reactjs.org/" />
                                                            <div className='eticket-card-ticketId'>Ticket ID T2022090834567890
                                                            <br/>* Show QR code to staff to check in.</div>
                                                        </div>
                                                    </div>)})}
                                                </Carousel>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Drawer>

                <Drawer open={add} className="event-addattendee">
                    <div className='add-attendee'>
                        <div className='add-attendee-header'>Add Attendee</div>
                        <div className='add-attendee-details'>
                            <div className='add-attendee-details-toggle'>
                                <div className={`add-attendee-details-toggle-pill ${change ? "active" : ""}`} onClick={() => setChange(true)}>Business</div>
                                <div className={`add-attendee-details-toggle-pill ${change ? "" : "active"}`} onClick={() => setChange(false)}>Guest</div>
                            </div>
                            <div className='add-attendee-details-form'>
                                <div className='add-attendee-details-form-guest'>
                                    <input id='input' placeholder='Full name' />
                                    <input id='input' placeholder='Email address' type='email' />
                                    <PhoneInput
                                        placeholder="123-456-789"
                                        value={value}
                                        onChange={setValue} />
                                    <select id="select">
                                        <option>How do you know your guest?</option>
                                    </select>
                                    <div className='import'>Import from Contacts</div>
                                </div>
                            </div>

                            <div className='add-attendee-buttons'>
                                <div className='add-button' onClick={() => setAdd(false)}>Cancel</div>
                                <div className={`add-button ${value ? "" : "gray"}`}>Add</div>
                            </div>
                        </div>
                    </div>
                </Drawer>

                <Drawer open={cancel} className="event-cancellation">
                    <div className='cancellationNote'>
                        <div className='title'>Cancel Registration</div>
                        <div className='msg'>Please contact the event organizer directly in order to cancel your registration.</div>
                        <div className='button'>
                            <div className='amway-button' onClick={() => setCancel(false)}>Close</div>
                        </div>
                    </div>
                </Drawer>
            </div>
            }

        </>

    )
}


export default EventDetails;